import buildDebug from 'debug'

import { errorUtils } from '@verdaccio/core'
import { Logger } from '@verdaccio/types'

import { encrypt, decrypt } from '../cipher'
import { type Database } from '../db'
import { userSecrets } from '../db/schema'
import { and, eq, isNull } from 'drizzle-orm'

const debug = buildDebug('verdaccio:plugin:apm:auth')

export class UserSecretsService {
  private db: Database
  private logger: Logger

  public constructor(database: Database, logger: Logger) {
    this.db = database
    this.logger = logger
  }

  public async count(): Promise<number> {
    const userCount = await this.db
      .$count(userSecrets, isNull(userSecrets.deleted))

    return userCount
  }

  public async add(user: string, hash: string, email: string): Promise<void> {
    try {
      const encryptedHash = await encrypt(user + hash)
      const encryptedEmail = await encrypt(email)
      const userSecret = { user, hash: encryptedHash, email: encryptedEmail }

      await this.db
        .insert(userSecrets)
        .values(userSecret)

      debug('user added successfully')
    }
    catch (error) {
      debug('user add error: %o', error)
      throw errorUtils.getInternalError(`Error adding user: ${error}`)
    }
  }

  public async remove(user: string): Promise<void> {
    try {
      await this.db
        .update(userSecrets)
        .set({ deleted: new Date() })
        .where(eq(userSecrets.user, user))

      debug('user removed successfully')
    }
    catch (error) {
      debug('user remove error: %o', error)
      throw errorUtils.getInternalError(`Error removing user: ${error}`)
    }
  }

  public async getHash(user: string): Promise<string | null> {
    const [userSecret] = await this.db
      .select({ hash: userSecrets.hash })
      .from(userSecrets)
      .where(and(eq(userSecrets.user, user), isNull(userSecrets.deleted)))

    if (!userSecret) {
      debug('user %s not found', user)
      return null
    }

    const decryptedHash = decrypt(user + userSecret.hash)
    return decryptedHash
  }

  public async getEmail(user: string): Promise<string | null> {
    const [userSecret] = await this.db
      .select({ email: userSecrets.email })
      .from(userSecrets)
      .where(and(eq(userSecrets.user, user), isNull(userSecrets.deleted)))

    if (!userSecret) {
      debug('user %s not found', user)
      return null
    }

    const decryptedEmail = decrypt(user + userSecret.email)
    return decryptedEmail
  }

  public async changePassword(user: string, hash: string): Promise<void> {
    try {
      const encryptedHash = await encrypt(user + hash)
      await this.db
        .update(userSecrets)
        .set({ hash: encryptedHash, updated: new Date(), deleted: null })
        .where(eq(userSecrets.user, user))

      debug('password changed successfully')
    }
    catch (error) {
      debug('password change error: %o', error)
      throw errorUtils.getInternalError(`Error changing password: ${error}`)
    }
  }

  public async changeEmail(user: string, email: string): Promise<void> {
    try {
      const encryptedEmail = await encrypt(email)

      await this.db
        .update(userSecrets)
        .set({ email: encryptedEmail, updated: new Date(), deleted: null })
        .where(eq(userSecrets.user, user))

      debug('email changed successfully')
    }
    catch (error) {
      debug('email change error: %o', error)
      throw errorUtils.getInternalError(`Error changing email: ${error}`)
    }
  }
}
