import buildDebug from 'debug'

import { errorUtils, pluginUtils } from '@verdaccio/core'
import { Config, Logger, PackageAccess, RemoteUser } from '@verdaccio/types'

import { getDatabase, type Database } from './db'
import { ENV } from './env'
import { UserSecretsService } from './services/user-secrets'
import { hashPassword, sanityCheck, verifyPassword } from './utils'

export interface AuthConfig {
  url?: string
  rounds: number
  max_users: number
  slow_verify_ms: number
}

export enum AuthDefaults {
  ROUNDS = 10,
  MAX_USERS = Infinity,
  SLOW_VERIFY_MS = 300,
}

const debug = buildDebug('verdaccio:plugin:pro:auth')

class AuthPlugin extends pluginUtils.Plugin<AuthConfig> implements pluginUtils.Auth<AuthConfig> {
  public config: Config
  public logger: Logger
  private authConfig: AuthConfig
  private db: Database
  private userSecretService: UserSecretsService

  public constructor(config: AuthConfig, options: pluginUtils.PluginOptions) {
    debug('start auth plugin')
    super(config, options)
    this.config = options.config
    this.logger = options.logger

    this.authConfig = {
      url: config.url || ENV.DATABASE_URL,
      rounds: config.rounds || AuthDefaults.ROUNDS,
      max_users: config.max_users || AuthDefaults.MAX_USERS,
      slow_verify_ms: config.slow_verify_ms || AuthDefaults.SLOW_VERIFY_MS,
    }

    if (!this.authConfig.url) {
      throw errorUtils.getServiceUnavailable(
        '[sql-storage] missing config. Add `store.@verdaccio-pro/sql-storage.url` to your config file or use environtment DATABASE_URL',
      )
    }

    this.db = getDatabase(this.authConfig.url, this.logger)
    this.userSecretService = new UserSecretsService(this.db, this.logger)

    debug('Verdaccio Pro auth plugin is enabled')
  }

  public async authenticate(user: string, password: string, cb: pluginUtils.AuthCallback): Promise<void> {
    debug('authenticate user %o', user)

    const hash = await this.userSecretService.getHash(user)
    if (!hash) {
      debug('user not found')
      return cb(null, false)
    }

    let passwordValid = false
    try {
      const start = new Date()
      passwordValid = await verifyPassword(password, hash)
      const durationMs = new Date().getTime() - start.getTime()
      if (durationMs > this.authConfig.slow_verify_ms) {
        debug('password took %sms to verify', durationMs)
        this.logger.warn(
          { user, durationMs },
          'Password for user "@{user}" took @{durationMs}ms to verify',
        )
      }
    }
    catch (error) {
      this.logger.error({ error }, 'Unable to verify user password: @{error.message}')
    }
    if (!passwordValid) {
      debug('invalid password')
      return cb(null, false)
    }

    debug('authentication succeeded!')
    // return all usergroups this user has access to;
    // (this particular plugin has no concept of usergroups, so just return user)
    return cb(null, [user])
  }

  // TODO: email is not part of the Auth interface (yet)
  public async adduser(user: string, password: string, cb: pluginUtils.AuthUserCallback, email?: string): Promise<void> {
    debug('add user %o', user)

    const count = await this.userSecretService.count()
    const oldHash = await this.userSecretService.getHash(user)
    const sanity = await sanityCheck(user, password, verifyPassword, oldHash, count, this.authConfig.max_users)

    debug('sanity check: %o', sanity)
    if (sanity) {
      return cb(sanity, false)
    }

    const newHash = await hashPassword(password, this.authConfig.rounds)

    try {
      await this.userSecretService.add(user, newHash, email || '')
      cb(null, true)
    }
    catch (error) {
      cb(error as errorUtils.VerdaccioError, false)
    }
  }

  public async removeUser(user: string): Promise<void> {
    debug('remove user %o', user)

    const hash = await this.userSecretService.getHash(user)
    if (!hash) {
      debug('user not found')
      throw errorUtils.getNotFound(`User '${user}' not found`)
    }

    await this.userSecretService.remove(user)
  }

  public async changePassword(
    user: string,
    oldPassword: string,
    newPassword: string,
    cb: pluginUtils.AuthChangePasswordCallback,
  ): Promise<void> {
    debug('change password for user %o', user)

    const hash = await this.userSecretService.getHash(user)
    if (!hash) {
      debug('user not found')
      return cb(null, false)
    }

    const passwordValid = await verifyPassword(oldPassword, hash)
    if (!passwordValid) {
      debug('invalid old password')
      throw new Error(`Unable to change password for user '${user}': invalid old password`)
    }

    const newHash = await hashPassword(newPassword, this.authConfig.rounds)

    try {
      await this.userSecretService.changePassword(user, newHash)
      cb(null, true)
    }
    catch (error) {
      cb(error as errorUtils.VerdaccioError, false)
    }
  }

  public async allow_access(user: RemoteUser, pkg: PackageAccess, cb: pluginUtils.AccessCallback): Promise<void> {
    // Everything is allowed
    cb(null, true)
  }

  public async allow_publish(user: RemoteUser, pkg: PackageAccess, cb: pluginUtils.AuthAccessCallback): Promise<void> {
    // Everything is allowed
    cb(null, true)
  }

  public async allow_unpublish(user: RemoteUser, pkg: PackageAccess, cb: pluginUtils.AuthAccessCallback): Promise<void> {
    // Everything is allowed
    cb(null, true)
  }
}

export default AuthPlugin
