import { config } from 'dotenv'
import z from 'zod'

const stringBoolean = z.coerce
  .string()
  .transform((val) => {
    return val === 'true'
  })
  .default(false)

const envSchema = z.object({
  NODE_ENV: z.string().default('development'),
  DATABASE_SECRET: z.string().trim().min(1),
  DATABASE_URL: z.string().trim().min(1),
  DB_POOL_SIZE: z.coerce.number().default(22),
  DB_SSL: stringBoolean.default(true),
  DB_SSL_REJECT_UNAUTHORIZED: stringBoolean.default(true),
  DB_LOGGING: stringBoolean,
  DB_SALT: z.string().default('saltypretzel'),
})

// Load environment variables from .env file
config({ debug: false, quiet: true })

const envServer = envSchema.safeParse({
  NODE_ENV: process.env.NODE_ENV,
  DATABASE_SECRET: process.env.DATABASE_SECRET,
  DATABASE_URL: process.env.DATABASE_URL,
  DB_POOL_SIZE: process.env.DB_POOL_SIZE,
  DB_SSL: process.env.DB_SSL,
  DB_SSL_REJECT_UNAUTHORIZED: process.env.DB_SSL_REJECT_UNAUTHORIZED,
  DB_LOGGING: process.env.DB_LOGGING,
  DB_SALT: process.env.DB_SALT,
})

if (!envServer.success) {
  throw new Error(envServer.error.message)
}

type Env = z.infer<typeof envSchema>

export const ENV: Env = envSchema.parse(process.env)
