import bcrypt from 'bcryptjs'
import buildDebug from 'debug'

import { errorUtils, API_ERROR, VerdaccioError } from '@verdaccio/core'

const debug = buildDebug('verdaccio:plugin:pro:auth')

/**
 * verifyPassword - matches password and it's hash.
 */
export async function verifyPassword(passwd: string, hash: string): Promise<boolean> {
  return await bcrypt.compare(passwd, hash)
}

/**
 * hashPassword - generates a hash for a password.
 */
export async function hashPassword(
  passwd: string,
  rounds: number,
): Promise<string> {
  const salt = await bcrypt.genSalt(rounds)
  return await bcrypt.hash(passwd, salt)
}

/**
 * Sanity check for a user
 */
export async function sanityCheck(
  user: string,
  passwd: string,
  verifyFn: (user: string, passwd: string) => Promise<boolean>,
  hash: string | null,
  count: number,
  maxUsers: number,
): Promise<VerdaccioError | null> {
  if (!user || !passwd) {
    debug('username or password is missing')
    return errorUtils.getBadRequest(API_ERROR.USERNAME_PASSWORD_REQUIRED) // 400
  }

  if (maxUsers < 0) {
    debug('registration is disabled')
    return errorUtils.getConflict(API_ERROR.REGISTRATION_DISABLED) // 409
  }

  if (hash) {
    const auth = await verifyFn(user, hash)
    if (auth) {
      debug(`user ${user} already exists`)
      return errorUtils.getConflict(API_ERROR.USERNAME_ALREADY_REGISTERED) // 409
    }
    debug(`user ${user} exists but password is wrong`)
    return errorUtils.getUnauthorized(API_ERROR.UNAUTHORIZED_ACCESS) // 401
  }
  else if (count >= maxUsers) {
    debug('maximum amount of users reached')
    return errorUtils.getForbidden(API_ERROR.MAX_USERS_REACHED)
  }

  debug('sanity check passed')
  return null
}
